.media-carousel {
  position: relative;
  height: 150px;
  background-color: rgba(255, 255, 255, 5%);
}

.media-carousel-controls {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0px 24px;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  pointer-events: none;
  background: linear-gradient(to right, rgba(0, 0, 0, 80%), transparent 100px), linear-gradient(to left, rgba(0, 0, 0, 80%), transparent 100px);
}

.media-carousel-controls .button-icon {
  width: 48px;
  height: 48px;
  color: var(--color-light);
  pointer-events: auto;
}

.media-carousel-thumbnails {
  display: flex;
  height: 100%;
  column-gap: 1px;
  overflow-y: hidden;
  overflow-x: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  user-select: none;
}

.media-carousel-thumbnails::-webkit-scrollbar {
  display: none;
}

@media screen and (max-width: 768px) {
  .media-carousel-controls {
    background: linear-gradient(to right, rgba(0, 0, 0, 80%), transparent 80px), linear-gradient(to left, rgba(0, 0, 0, 80%), transparent 80px);
  }

  .media-carousel-controls .button-icon {
    width: 32px;
    height: 32px;
  }
}
